/* from style guide */
/* not in style guide but in designs */
/* from old RM */
#title[data-v-773eabac] {
  color: #333333;
  margin: 1rem 0 0.8rem 0;
  font-size: 1.625rem;
}
.splash-container[data-v-773eabac] {
  background: #1f8dd6;
  z-index: 1;
  overflow: hidden;
  /* The following styles are required for the "scroll-over" effect */
  width: 100%;
  height: 91%;
  top: 0;
  left: 0;
  position: fixed !important;
}
.splash[data-v-773eabac] {
  /* absolute center .splash within .splash-container */
  width: 80%;
  height: 50%;
  margin: auto;
  position: absolute;
  top: 100px;
  left: 0;
  bottom: 0;
  right: 0;
  text-align: center;
  text-transform: uppercase;
}

/* This is the main heading that appears on the blue section */
.splash-head[data-v-773eabac] {
  font-size: 20px;
  font-weight: bold;
  color: white;
  border: 3px solid white;
  padding: 1em 1.6em;
  font-weight: 100;
  border-radius: 5px;
  line-height: 1em;
}

/* This is the subheading that appears on the blue section */
.splash-subhead[data-v-773eabac] {
  color: white;
  letter-spacing: 0.05em;
  opacity: 0.8;
}



